.page-chat .tab-screen {
  min-height: calc(100vh - 0.533333rem);
}

.page-chat .chat-list {
  overflow: hidden;
}

.page-chat .chat-item {
  display: -ms-flexbox;
  display: flex;
  padding: 0.133333rem;
  background: #fff;
  border-bottom: 0.013333rem solid #eee;
}

.page-chat .avatar {
  width: 0.533333rem;
  height: 0.533333rem;
  border-radius: 50%;
  margin-right: 0.133333rem;
}

.page-chat .msg {
  font-size: 0.133333rem;
  color: #666;
  margin-top: 0.08rem;
}

.page-chat .item-name {
  font-size: 0.186667rem;
  margin-bottom: 0.053333rem;
}

.page-chat .login-tip {
  text-align: center;
  width: 2rem;
  height: 0.586667rem;
  line-height: 0.586667rem;
  margin: 0 auto;
  margin-top: 50%;
  background: #f44336;
  color: #fff;
  font-size: 0.213333rem;
  border-radius: 0.026667rem;
}


