.navbar {
  position: -webkit-sticky;
  position: sticky;
  z-index: 25;
  left: 0;
  top: 0;
  border-bottom: 0.013333rem solid #e5e5e5;
}

.navbar .am-navbar-left {
  padding-left: 0.213333rem;
}

.navbar .am-icon-md {
  position: relative;
  left: -0.133333rem;
  width: 0.4rem;
  height: 0.4rem;
  color: #666;
}


